@import '../common/styles/variables';
.skillsContainer {
    /*justify-content: flex-start;*/
    /*flex-direction: column;*/
    padding-bottom: 20px;
}
.skillsHeader {
    width: 100%;
    text-align: center;
    span {
        font-family: $basicFont;
        font-size: $titleFontSize - 5px;
        color: $light-grey;
    }
}
.skills {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: space-evenly;
    margin-top: 25px;
}
.softSkills {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: space-evenly;
    margin-top: 25px;
}
@media (max-width: 400px) {
    .skillsBlock {
        height: inherit;
        padding-bottom: 10px;
    }
    .skillsContainer {
        display: flex;
        justify-content: space-evenly;
        flex-direction: unset;
    }
}
