@import '../common/styles/variables';

.headerDiv {
    display: block;
    width: 100%;
    min-height: 45px;
    max-height: inherit;
    background: #fff;
    border-bottom: 1px solid #f2f2f2;
    position: fixed;
    z-index: 2;
    top: 0;
    animation-duration: 0.5s;
    animation-fill-mode: both;
    animation-name: fadeOut;
    //transition: all 0.3s ease-in-out;

    .menuButton {
        top: 15.5px;
        cursor: pointer;
        display: block;
        width: 30px;
        height: 14px;
        right: 10px;
        position: absolute;

        .before {
            width: 25px;
            margin-bottom: 4px;
            display: block;
            height: 2px;
            background: #212121;
            transition: transform 0.7s;
        }

        .main {
            width: 20px;
            margin-bottom: 4px;
            display: block;
            height: 2px;
            background: #212121;
            transition: transform 0.7s;
        }

        .after {
            width: 30px;
            display: block;
            height: 2px;
            background: #212121;
            transition: transform 0.7s;
        }
    }
}
.menuButtonActive {
    top: 15.5px;
    cursor: pointer;
    display: block;
    width: 30px;
    height: 14px;
    right: 10px;
    position: absolute;

    .before {
        display: block;
        height: 2px;
        background: #212121;
        width: 20px;
        margin: 0;
        transition: transform 0.7s;
        transform: translateY(2px) rotate(45deg);
    }

    .main {
        display: none;
    }

    .after {
        display: block;
        height: 2px;
        background: #212121;
        width: 20px;
        margin: 0;
        transition: transform 0.7s;
        transform: translateY(0) rotate(-45deg);
    }
}
