@import 'src/common/styles/variables';
.skill {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 110px;
    height: 110px;
    h3 {
        @include basicText();
        position: absolute;
        bottom: 0;
    }
}
.skillLogo {
    width: 50px;
    height: 50px;
    transition: all 1s ease;
    border-radius: 100%;
    &:hover {
        transform: scale(1.5);
    }
}
.skillTitle {
    color: #212121;
    font-size: 12px;
    margin-top: 8px;
    letter-spacing: 0.05em;
    text-align: center;
    span {
        font-family: $basicFont;
        color: $dark-grey;
        font-size: $basicFontSize;
    }

    //.skillPercentage{
    //    position: absolute;
    //    right: 0;
    //    color: #777777;
    //}
}

.skillLoader {
    width: 200px;
    height: 5px;
    border-radius: 5px;
    background: linear-gradient(to right, #ed3200 70%, #cecbcb 30%);
}
.bar {
    height: 3px;
    background: #f2f2f2;
    border-radius: 3px;
}
.barFill {
    background: #ed3200;
    height: 3px;
    width: 80%;
    border-radius: 3px;
}

@media (max-width: 530px) {
    .skill {
        width: 80px;
        height: 100px;
    }
    .skillLogo {
        width: 40px;
        height: 40px;
    }
}
